import ajax from './../api'

export default {
    //
    get_columns(context) {
        ajax.get(API + 'index')
            .then(data => {
                context.commit('set_columns', data.catagory);
            })
            .catch(err => {
                console.log(err);
            })
    },
    //logo
    get_logo(context) {
        context.commit('set_logo', './../static/img/logo.jpg');
    },
    //banner
    get_banner(context) {
        ajax.get(API + 'slide')
            .then(data => {
                context.commit('set_banner', data.slide);
            })
            .catch(err => {
                console.log(err);
            })
    },
    //
    get_aboutUs(context) {
        ajax.get(API + 'about')
            .then(data => {
                context.commit('set_aboutUs', data.array);
            })
            .catch(err => {
                console.log(err);
            })
    },
    //
    get_news(context) {
        ajax.get(API + 'stock')
            .then(data => {
                context.commit('set_news', data.array);
            })
            .catch(err => {
                console.log(err);
            })
    },
    //footer
    get_forumColumns(context) {
        ajax.get(API + 'manger')
            .then(data => {
                context.commit('set_forumColumns', data.array);
            })
            .catch(err => {
                console.log(err);
            })
    },
    //footer
    get_footer(context) {
        ajax.get(API + 'message')
            .then(data => {

                context.commit('set_footer', data.array);
            })
            .catch(err => {
                console.log(err);
            })
    },  

    //
    get_webtips(context) {
        ajax.get(API + 'message')
            .then(data => {
                context.commit('set_webtips', data.array);
            })
            .catch(err => {
                console.log(err);
            })
    }
}


